import { Floor } from './floor';

export class Level {
  constructor(options) {
    this.playerPos = options.playerPos;
    this.scrolling = options.scrolling;
    this.loader = options.loader;
    this.background = options.background;
    this.floorSprite = options.floorSprite;
    this.dangerSprite = options.dangerSprite; //  

    this.statics = []; //   (, )
    this.scenery = []; //  (, )
    this.blocks = []; //  
    this.enemies = []; // 
    this.items = []; //  (, power-ups)
    this.pipes = []; //   

    //         
    this.allDangerousX = options.allDangerousX || new Set(); //  
    this.holeX = options.holeX || new Set(); //  

    for (var i = 0; i < 15; i++) {
      this.statics[i] = [];
      this.scenery[i] = [];
      this.blocks[i] = [];
    }
  }

  isDangerousBlock(i) {
    //      (hole)    ,
    //      .
    return this.allDangerousX.has(i) || this.holeX.has(i); //  
  }

  putFloor(start, end) {
    //  
    for (let i = start; i < end; i++) {
      //     
      const isDangerous = this.isDangerousBlock(i);

      if (!isDangerous) {
        // ,  
        this.statics[13][i] = new Floor([16 * i, 208], this.floorSprite);
        this.statics[14][i] = new Floor([16 * i, 224], this.floorSprite);
      } else {
        //   (,   ,   )
        this.statics[13][i] = new Floor([16 * i, 208], this.dangerSprite);
        this.statics[14][i] = new Floor([16 * i, 224], this.dangerSprite);
      }
    }
  }
}
